<template>
  <div>
    <sub-nav-bar
      :subNavlist="subNavlist"
    />
    <div class="task-list">
      <task-item v-for="i in 10" :key="i"></task-item>
    </div>
  </div>
</template>
<script>
  import { SubNavBar } from '../layout/components'
  import taskItem from '../../components/item/task-item'
  export default {
    name: 'name',
    data() {
      return {
        subNavlist: [
          {
            name: '最新任务',
            type: 0
          },
          {
            name: '高额任务',
            type: 1
          },
          {
            name: '拼团模式',
            type: 1
          },
          {
            name: '一元夺宝',
            type: 1
          }
        ]
      }
    },
    components: {
      SubNavBar,
      taskItem
    }
  }
</script>

<style>

</style>



